<template>
  <div class="lost-item-register">
    <h1>üìù Â§±Áâ©ÁôªËÆ∞</h1>

    <el-form
        :model="form"
        ref="formRef"
        label-width="120px"
        :rules="rules"
        label-position="top"
    >
      <!-- Ë°®ÂçïÈ°π -->
      <el-form-item label="Áâ©ÂìÅÊ†áÈ¢ò" prop="title">
        <el-input
            v-model="form.title"
            placeholder="ËØ∑ËæìÂÖ•Áâ©ÂìÅÂêçÁß∞ÔºàÂ¶ÇÔºöÈªëËâ≤Âçé‰∏∫ÊâãÊú∫Ôºâ"
        />
      </el-form-item>

      <el-form-item label="ËØ¶ÁªÜÊèèËø∞" prop="description">
        <el-input
            type="textarea"
            :rows="4"
            v-model="form.description"
            placeholder="ËØ∑Â∞ΩÂèØËÉΩËØ¶ÁªÜÊèèËø∞Áâ©ÂìÅÁâπÂæÅÔºàÂ¶ÇÔºöÂûãÂè∑„ÄÅÁâπÊÆäÊ†áËÆ∞Á≠âÔºâ"
            show-word-limit
            maxlength="300"
        />
      </el-form-item>

      <!-- Êó•ÊúüÈÄâÊã© -->
      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item label="‰∏¢Â§±Êó∂Èó¥" prop="lost_time">
            <el-date-picker
                v-model="form.lost_time"
                type="datetime"
                format="yyyy-MM-dd HH:mm"
                value-format="yyyy-MM-ddTHH:mm"
                placeholder="ÈÄâÊã©ÂÖ∑‰ΩìÊó∂Èó¥"
            />

          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="‰∏¢Â§±Âú∞ÁÇπ" prop="location">
            <el-input
                v-model="form.location"
                placeholder="‰æãÂ¶ÇÔºö3Âè∑ÊïôÂ≠¶Ê•º201ÊïôÂÆ§"
            />
          </el-form-item>
        </el-col>
      </el-row>

      <!-- ÂàÜÁ±ªÈÄâÊã© -->
      <el-form-item label="Áâ©ÂìÅÂàÜÁ±ª" prop="category">
        <el-select
            v-model="form.category"
            placeholder="ËØ∑ÈÄâÊã©ÊúÄÂåπÈÖçÁöÑÂàÜÁ±ª"
            filterable
        >
          <el-option
              v-for="category in categories"
              :key="category.id"
              :label="category.name"
              :value="category.id"
          />
        </el-select>
      </el-form-item>

      <!-- ËÅîÁ≥ªÊñπÂºè -->
      <el-form-item label="ËÅîÁ≥ªÊñπÂºè" prop="contact">
        <el-input
            v-model="form.contact"
            placeholder="ÊâãÊú∫Âè∑ÊàñÈÇÆÁÆ±"
        />
      </el-form-item>

      <!-- ÂõæÁâá‰∏ä‰º† -->
      <el-form-item label="Áâ©ÂìÅÁÖßÁâá">
        <el-upload
            action="#"
            list-type="picture-card"
            :auto-upload="false"
            :on-change="handleUploadSuccess"
            :file-list="form.images"
            :before-upload="beforeUpload"
        >
          <i class="el-icon-plus"/>
        </el-upload>
        <div class="el-upload__tip">
          ÊîØÊåÅ‰∏ä‰º† JPG/PNG Ê†ºÂºèÂõæÁâáÔºåÂçïÂº†‰∏çË∂ÖËøá5MB
        </div>
      </el-form-item>

      <!-- Êìç‰ΩúÊåâÈíÆ -->
      <el-form-item>
        <el-button
            type="primary"
            class="submit-btn"
            :loading="isSubmitting"
            @click="submitForm"
        >
          {{ isSubmitting ? 'Êèê‰∫§‰∏≠...' : 'Á´ãÂç≥ÁôªËÆ∞' }}
        </el-button>
      </el-form-item>
    </el-form>
    <!-- Âú®Ê®°ÊùøÂ∫ïÈÉ®Ê∑ªÂä†Ê≠§ÂØπËØùÊ°Ü -->
    <el-dialog
        title="‚úÖ ÁôªËÆ∞ÊàêÂäü"
        :visible.sync="dialogVisible"
        width="700px"
        @closed="handleDialogClosed"
    >
      <el-descriptions
          :column="2"
          border
          label-class-name="detail-label"
      >
        <!-- ÂºπÁ™óÂÜÖÂÆπ -->
        <el-descriptions-item label="Áâ©ÂìÅÊ†áÈ¢ò">{{ submittedItem.title }}</el-descriptions-item>
        <el-descriptions-item label="ÂàÜÁ±ª">
          {{ getCategoryName(submittedItem.category) }}
        </el-descriptions-item>
        <el-descriptions-item label="‰∏¢Â§±Êó∂Èó¥">
          {{ submittedItem.lost_time }}
        </el-descriptions-item>
        <el-descriptions-item label="‰∏¢Â§±Âú∞ÁÇπ">{{ submittedItem.location }}</el-descriptions-item>
        <el-descriptions-item label="ËÅîÁ≥ªÊñπÂºè" :span="2">
          <el-link type="primary">{{ submittedItem.contact }}</el-link>
        </el-descriptions-item>
        <el-descriptions-item label="ËØ¶ÁªÜÊèèËø∞" :span="2">
          <pre class="description-pre">{{ submittedItem.description }}</pre>
        </el-descriptions-item>
        <el-descriptions-item label="Áâ©ÂìÅÁÖßÁâá" :span="2" v-if="submittedItem.images?.length">
          <el-image
              v-for="(img, index) in submittedItem.images"
              :key="index"
              :src="img.url"
              fit="cover"
              class="detail-image"
          />
        </el-descriptions-item>
      </el-descriptions>
      <!-- Â∫ïÈÉ®Êìç‰ΩúÊåâÈíÆ -->
      <span slot="footer">
      <el-button
          type="success"
          @click="handleConfirm"
      >
        ÊâìÂç∞ÂõûÊâß (Ctrl+P)
      </el-button>
      <el-button
          type="primary"
          @click="dialogVisible = false"
      >
        Á°ÆÂÆöËøîÂõû
      </el-button>
    </span>
    </el-dialog>
  </div>

</template>


<script>
import axios from 'axios';

export default {
  data() {
    return {
      // Êñ∞Â¢ûÊâãÊú∫Âè∑È™åËØÅËßÑÂàô
      phoneRules: [
        {required: true, message: 'ËØ∑ËæìÂÖ•ËÅîÁ≥ªÊñπÂºè', trigger: 'blur'},
        {
          pattern: /^(1[3-9]\d{9}|[\w-]+@[\w-]+\.[\w-]+)$/,
          message: 'ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑÊâãÊú∫Âè∑ÊàñÈÇÆÁÆ±',
          trigger: 'blur'
        }
      ],
      form: {
        title: '',
        description: '',
        lost_time: null,
        location: '',
        category: null,
        contact: '',
        images: []
      },
      categories: [],
      fileList: [],
      rules: {
        title: [
          {required: true, message: 'ËØ∑ËæìÂÖ•Ê†áÈ¢ò', trigger: 'blur'}
        ],
        description: [
          {required: true, message: 'ËØ∑ËæìÂÖ•ËØ¶ÁªÜÊèèËø∞', trigger: 'blur'}
        ],
        lost_time: [
          {required: true, message: 'ËØ∑ÈÄâÊã©‰∏¢Â§±Êó∂Èó¥', trigger: 'change'}
        ],
        location: [
          {required: true, message: 'ËØ∑ËæìÂÖ•Âú∞ÁÇπ', trigger: 'blur'}
        ],
        category: [
          {required: true, message: 'ËØ∑ÈÄâÊã©Áâ©ÂìÅÂàÜÁ±ª', trigger: 'change'}
        ],
        contact: [
          {required: true, message: 'ËØ∑ËæìÂÖ•ËÅîÁ≥ªÊñπÂºè', trigger: 'blur'}
        ]
      },
      isSubmitting: false,
      dialogVisible: false,
      submittedItem: {}, // Â≠òÂÇ®ÊúçÂä°Âô®ËøîÂõûÁöÑÂÆåÊï¥Êï∞ÊçÆ
      printStyle: `
      @media print {
        body * { visibility: hidden; }
        .el-dialog { width: 210mm!important; visibility: visible; }
        .el-dialog__header, .el-dialog__footer { display: none; }
      }
    `

    }
  },
  mounted() {
    this.fetchCategories()
    document.addEventListener('keydown', (e) => {
      if (e.ctrlKey && e.key === 'p' && this.dialogVisible) {
        e.preventDefault()
        this.handleConfirm()
      }
    })

  },
  methods: {
    // ÂàÜÁ±ªIDËΩ¨ÂêçÁß∞ÔºàÂåπÈÖçÁî®Êà∑ÂéüÊúâÂàÜÁ±ªÊï∞ÊçÆÔºâ
    getCategoryName(categoryId) {
      return this.categories.find(item => item.id === categoryId)?.name || 'Êú™Áü•ÂàÜÁ±ª'
    },

    // Â§ÑÁêÜÂºπÁ™óÂÖ≥Èó≠ÂêéÁöÑÊìç‰Ωú
    handleDialogClosed() {
      this.$router.go(-1) // ÊàñËá™ÂÆö‰πâË∑≥ËΩ¨ÈÄªËæë
    },

    // ÊâìÂç∞ÂäüËÉΩ
    handleConfirm() {
      const printWindow = window.open('', '_blank')
      printWindow.document.write(`
      <style>${this.printStyle}</style>
      ${document.querySelector('.el-dialog').outerHTML}
    `)
      printWindow.print()
      printWindow.close()
    },

    async fetchCategories() {
      try {
        const response = await axios.get('/api/categories/')
        this.categories = response.data
      } catch (error) {
        console.error('Ëé∑ÂèñÁâ©ÂìÅÂàÜÁ±ªÂ§±Ë¥•:', error)
        this.$message.error('Ëé∑ÂèñÁâ©ÂìÅÂàÜÁ±ªÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï„ÄÇ')
      }
    },
    // ‰ºòÂåñÂêéÁöÑÊèê‰∫§ÊñπÊ≥ï
    async submitForm() {
      try {
        this.isSubmitting = true;
        // Áªü‰∏ÄÊèê‰∫§Êï∞ÊçÆÔºàÂê´ÂõæÁâáÔºâ
        const formData = new FormData();
        Object.keys(this.form).forEach(key => {
          if (key !== 'images') {
            formData.append(key, this.form[key]);
          }
        });
        // Ê∑ªÂä†ÂõæÁâáÊñá‰ª∂
        this.form.images.forEach((file) => {
          formData.append('images', file.raw);
        });
        const response = await axios.post('/api/items/lost/', formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
            'Authorization': `Token ${this.$store.state.token}`
          }
        });
        // ‰øùÂ≠òÊúçÂä°Âô®ËøîÂõûÁöÑÂÆåÊï¥Êï∞ÊçÆ
        this.submittedItem = {
          ...response.data,
          images: this.form.images // ‰øùÁïôÂâçÁ´Ø‰∏¥Êó∂È¢ÑËßàÂõæ
        };

        // Ê∏ÖÁ©∫Ë°®ÂçïÔºàÊ†πÊçÆÈúÄÊ±ÇÈÄâÊã©‰øùÁïôÊàñÊ∏ÖÈô§Ôºâ
        this.$refs.formRef.resetFields();
        this.form.images = [];

        this.dialogVisible = true; // ÊòæÁ§∫ÂºπÁ™ó

        this.$message.success({
          message: 'ÁôªËÆ∞ÊàêÂäüÔºå3ÁßíÂêéËá™Âä®Ë∑≥ËΩ¨',
          duration: 3000
        });
      } catch (error) {
        const msg = error.response?.data?.detail || 'Êèê‰∫§Â§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•';
        this.$message.error(msg);
      } finally {
        this.isSubmitting = false;
      }
    },

    // Â¢ûÂº∫ÁöÑÊñá‰ª∂‰∏ä‰º†Â§ÑÁêÜ
    handleUploadSuccess(file) {
      if (!file || !file.raw) {
        this.$message.error('Êñá‰ª∂Âä†ËΩΩÂºÇÂ∏∏')
        return
      }
      try {
        // ÊòæÁ§∫10ÁßíÂÜÖÊúâÊïàÁöÑÈ¢ÑËßàÈìæÊé•
        const previewUrl = URL.createObjectURL(file.raw)
        const fileData = {
          uid: file.uid,        // ÂøÖÈ°ªÂåÖÂê´uid
          name: file.name,
          status: 'ready',      // ÊâãÂä®ÁÆ°ÁêÜÁä∂ÊÄÅ
          percentage: 0,        // ËøõÂ∫¶Êù°ÂàùÂßãÂÄº
          url: previewUrl,
          raw: file.raw
        }

        this.form.images = [...this.form.images, fileData]
      } catch (error) {
        console.error('Êñá‰ª∂È¢ÑËßàÈîôËØØ:', error)
        this.$message.error('‰∏çÊîØÊåÅËØ•Êñá‰ª∂Á±ªÂûã')
      }
    },


    beforeUpload(file) {
      const isValidType = ['image/jpeg', 'image/png'].includes(file.type)
      const isLt5M = file.size / 1024 / 1024 < 5
      if (!isValidType) {
        this.$message.error('‰ªÖÊîØÊåÅ JPG/PNG Ê†ºÂºè')
        return false
      }
      if (!isLt5M) {
        this.$message.error('ÂõæÁâáÂ§ßÂ∞è‰∏çËÉΩË∂ÖËøá5MB')
        return false
      }
      return true
    },
  }
}
</script>

<style lang="scss" scoped>
// ÈÖçËâ≤ÊñπÊ°à
$primary-color: #409EFF;
$error-color: #F56C6C;
$success-color: #67C23A;
$text-primary: #303133;
$text-secondary: #606266;
$border-color: #EBEEF5;
$bg-color: #f6f8fa;

.lost-item-register {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px 24px;
  background: white;
  border-radius: 12px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.08);

  h1 {
    text-align: center;
    color: $text-primary;
    font-size: 28px;
    margin-bottom: 40px;
    position: relative;

    &::after {
      content: '';
      display: block;
      width: 60px;
      height: 3px;
      background: $primary-color;
      margin: 12px auto 0;
      border-radius: 2px;
    }
  }

  // Ë°®ÂçïÈ°πËÅöÁÑ¶Áä∂ÊÄÅ
  .el-form-item {
    margin-bottom: 28px;

    &:hover {
      .el-form-item__label::before {
        color: $primary-color;
      }
    }
  }

  // ËæìÂÖ•Ê°ÜÊ†∑Âºè‰ºòÂåñ
  .el-input, .el-textarea, .el-select {
    .el-input__inner, .el-textarea__inner {
      border-radius: 8px;
      transition: all 0.3s;

      &:focus {
        border-color: $primary-color;
        box-shadow: 0 0 8px rgba($primary-color, 0.2);
      }
    }
  }

  // ÂõæÁâá‰∏ä‰º†Ê†∑Âºè
  .el-upload {
    &__tip {
      color: $text-secondary;
      font-size: 12px;
      margin-top: 8px;
    }

    &-list {
      &__item {
        transition: all 0.3s;
        border-radius: 8px;

        &:hover {
          background-color: rgba($primary-color, 0.05);
        }
      }
    }
  }

  // Êèê‰∫§ÊåâÈíÆ
  .submit-btn {
    width: 100%;
    padding: 14px;
    font-size: 16px;
    letter-spacing: 1px;
    transition: all 0.3s;

    &:hover {
      transform: translateY(-2px);
      box-shadow: 0 4px 12px rgba($primary-color, 0.3);
    }
  }

  // ÂìçÂ∫îÂºèÈÄÇÈÖç
  @media (max-width: 768px) {
    padding: 24px 16px;
    margin: 0 16px;

    h1 {
      font-size: 24px;
    }

    .el-form-item__label {
      text-align: left !important;
      margin-bottom: 8px;
    }
  }
}

// ÂõæÁâáÈ¢ÑËßàÊ†∑Âºè
.image-preview-container {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  margin-top: 16px;

  .preview-item {
    position: relative;
    width: 100px;
    height: 100px;
    border-radius: 8px;
    overflow: hidden;
    transition: all 0.3s;

    &:hover {
      .delete-btn {
        opacity: 1;
      }
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    .delete-btn {
      position: absolute;
      right: 4px;
      top: 4px;
      opacity: 0;
      padding: 4px;
      background: rgba(black, 0.6);
      border: none;
      transition: opacity 0.3s;

      i {
        color: white;
        font-size: 14px;
      }
    }
  }
}

// Âú®<style>Ê†áÁ≠æ‰∏≠Ê∑ªÂä†Ôºö
.el-dialog {
  border-radius: 12px;

  &__header {
    background: #f8f9fa;
    border-radius: 12px 12px 0 0;
    padding: 20px;
  }

  &__body {
    padding: 30px;
  }
}

.detail-label {
  width: 100px;
  text-align: justify;
  text-justify: inter-ideograph;
  font-weight: 500;

  &::after {
    content: "Ôºö"
  }
}

.description-pre {
  white-space: pre-wrap;
  line-height: 1.6;
  margin: 0;
  font-family: inherit;
}

.detail-image {
  width: 150px;
  height: 150px;
  margin-right: 12px;
  border: 1px solid #ebeef5;
  border-radius: 8px;
  transition: transform 0.3s;

  &:hover {
    transform: scale(1.05);
    cursor: zoom-in;
  }
}

</style>
